.btn {
  display: inline-block;
  outline: none;
  overflow: visible;
  cursor: pointer;
  line-height: 100%;
  padding: 12px 18px;
  font-size: 14px;
  text-align: center;
  border: 2px solid #001849;
  text-transform: uppercase;
  transition: 0.2s;
  font-family: 'Montserrat','Helvetica', Arial,sans-serif;
  font-weight: 700;
  font-style: normal;
}
.btn:before {
  display: none;
}
.btn-primary {
  background: #001849;
  color: #FFFFFF;
}
.btn-primary:hover {
  color: #FFFFFF;
  background: #00297c;
  border-color: #00297c;
}
.btn-primary:active {
  color: #FFFFFF;
  background: #00102f;
}
.btn-primary:disabled {
  opacity: 0.3;
}
.btn-secondary,
.block.blue .btn-primary,
section.split-section .split-section--left .btn-primary,
section.split-section.reverse .split-section--right .btn-primary {
  background: #FFFFFF;
  color: #001849;
}
.btn-secondary:hover,
.block.blue .btn-primary:hover,
section.split-section .split-section--left .btn-primary:hover,
section.split-section.reverse .split-section--right .btn-primary:hover {
  color: #FFFFFF;
  background: #00297c;
  border-color: #00297c;
}
.btn-secondary:active,
.block.blue .btn-primary:active,
section.split-section .split-section--left .btn-primary:active,
section.split-section.reverse .split-section--right .btn-primary:active {
  color: #FFFFFF;
  background: #ffffff;
}
.btn-secondary:disabled,
.block.blue .btn-primary:disabled,
section.split-section .split-section--left .btn-primary:disabled,
section.split-section.reverse .split-section--right .btn-primary:disabled {
  opacity: 0.3;
}
