form {
  position: relative;
}
form .form-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
form .form-row .control-group {
  width: 100%;
  margin: 0 0 16px;
}
form .form-row .control-group br {
  display: none;
}
form .form-row .control-group.half {
  width: 100%;
  margin: 0 0 16px 0;
}
form .form-row .control-group.half:last-of-type {
  margin-right: 0;
}
form .form-row .wpcf7-response-output.wpcf7-validation-errors {
  border: none;
  border-top: 2px solid #FF4747;
  border-bottom: 2px solid #FF4747;
  color: #FF4747;
  font-size: 14px;
  text-transform: uppercase;
  width: 100%;
  text-align: center;
  margin: 0 0 12px;
  font-family: 'Montserrat','Helvetica', Arial,sans-serif;
  font-weight: 700;
  font-style: normal;
}
form .form-row .wpcf7-response-output.wpcf7-mail-sent-ok {
  border: none;
  border-top: 2px solid #0d9671;
  border-bottom: 2px solid #0d9671;
  color: #0d9671;
  font-size: 14px;
  text-transform: uppercase;
  width: 100%;
  text-align: center;
  margin: 0 0 12px;
  font-family: 'Montserrat','Helvetica', Arial,sans-serif;
  font-weight: 700;
  font-style: normal;
}
form .form-row .wpcf7-not-valid-tip {
  display: block;
  margin-top: 4px;
  color: #FF4747;
  font-size: 12px;
  text-transform: uppercase;
  font-family: 'Montserrat','Helvetica', Arial,sans-serif;
  font-weight: 600;
  font-style: normal;
}
form .form-row label {
  display: block;
  font-size: 14px;
  color: #001849;
  text-transform: uppercase;
  margin: 0 0 6px;
  font-family: 'Montserrat','Helvetica', Arial,sans-serif;
  font-weight: 700;
  font-style: normal;
}
form .form-row input[type=text],
form .form-row input[type=email],
form .form-row textarea,
form .form-row .custom-select {
  appearance: none;
  border: 1px solid #CCCCCC;
  background: #FFFFFF;
  color: #333333;
  font-size: 16px;
  padding: 8px 16px;
  border-radius: 0;
  width: 100%;
  line-height: 100%;
}
form .form-row input[type=text]::-webkit-input-placeholder,
form .form-row input[type=email]::-webkit-input-placeholder,
form .form-row textarea::-webkit-input-placeholder,
form .form-row .custom-select::-webkit-input-placeholder {
  color: #666666;
}
form .form-row input[type=text]:-moz-placeholder,
form .form-row input[type=email]:-moz-placeholder,
form .form-row textarea:-moz-placeholder,
form .form-row .custom-select:-moz-placeholder {
  color: #666666;
}
form .form-row input[type=text]::-moz-placeholder,
form .form-row input[type=email]::-moz-placeholder,
form .form-row textarea::-moz-placeholder,
form .form-row .custom-select::-moz-placeholder {
  color: #666666;
}
form .form-row input[type=text]:-ms-input-placeholder,
form .form-row input[type=email]:-ms-input-placeholder,
form .form-row textarea:-ms-input-placeholder,
form .form-row .custom-select:-ms-input-placeholder {
  color: #666666;
}
form .form-row input[type=text]:focus,
form .form-row input[type=email]:focus,
form .form-row textarea:focus,
form .form-row .custom-select:focus {
  outline: none;
  border: 1px solid #001849;
}
form .form-row textarea {
  min-width: 100%;
  max-width: 100%;
  min-height: 120px;
}
form .form-row .ajax-loader {
  display: block !important;
  position: absolute;
  top: 0;
  left: 0;
  width: 100% !important;
  height: 100% !important;
  background-color: rgba(255, 255, 255, 0.5) !important;
  background-image: url('../../dist/img/loading.svg') !important;
  background-position: center center;
  background-repeat: no-repeat;
  margin: 0 !important;
}
