.wrapper {
  position: relative;
  width: 100%;
  max-width: 980px;
  padding: 0 30px;
  margin: 0 auto;
}
.wrapper.large {
  max-width: 1024px;
}
.block {
  display: block;
  position: relative;
  overflow: hidden;
  width: 100%;
}
.block .logo-inverse {
  display: block;
  position: absolute;
  top: -100%;
  max-width: 60px;
  transition: 0s;
  z-index: 99;
  transform: translate3d(0, 0, 0);
}
.block .logo-inverse svg {
  display: block;
  width: 100%;
  height: auto;
}
.block .logo-inverse svg .logo-mark-path {
  fill: #001849;
}
.block .menu-trigger-inverse {
  display: block;
  position: absolute;
  top: -100%;
  z-index: 9999;
  width: 22px;
  height: 34px;
  transition: 0s;
  transform: translate3d(0, 0, 0);
}
.block .menu-trigger-inverse:before {
  display: none;
}
.block .menu-trigger-inverse span {
  display: block;
  width: 22px;
  height: 2px;
  background: #001849;
  transition: background 0s .2s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.block .menu-trigger-inverse span:before,
.block .menu-trigger-inverse span:after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #001849;
  transition-property: transform;
  transition-duration: 0.2s, 0.2s;
  transition-delay: 0.2s, 0s;
}
.block .menu-trigger-inverse span:before {
  top: -7px;
  transition-property: top, transform;
}
.block .menu-trigger-inverse span:after {
  bottom: -7px;
  transition-property: bottom, transform;
}
.block.padded {
  padding: 30px 0;
}
.block.padded-top {
  padding: 30px 0 0;
}
.block.padded-bottom {
  padding: 0 0 30px;
}
.block.grey {
  background: #FAFAFA;
}
.block.blue {
  background: #001849;
}
.block.blue h2,
.block.blue h3,
.block.blue p,
.block.blue span,
.block.blue a,
.block.blue ul li,
.block.blue ol li {
