/*************************
        Tease Post
**************************/
article.tease-post {
  padding: 12px;
  transition: 0.2s;
}
article.tease-post:hover {
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.08);
}
article.tease-post a:before {
  display: none;
}
article.tease-post .tease-image {
  display: block;
  margin: 0 0 30px;
}
article.tease-post .tease-image img {
  display: block;
  width: 100%;
  margin: 0 auto;
}
article.tease-post .tease-video {
  display: block;
  margin: 0 0 22px;
}
article.tease-post .tease-info {
  display: block;
}
article.tease-post .tease-details {
  display: flex;
  flex-direction: row;
  align-items: center;
