/*********************
        Header
**********************/
header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 16px;
  min-height: 90px;
  z-index: 99;
}
header .header--left {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  position: relative;
}
header .logo {
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  max-width: 60px;
  transform: translateY(-50%);
  transition: none;
}
header .logo svg {
  display: block;
  width: 100%;
  height: auto;
}
header .logo svg .logo-mark-path {
  fill: #FFFFFF;
}
header .menu-trigger {
  display: block;
  position: absolute;
  right: 0;
  top: 50%;
  z-index: 9999;
  width: 22px;
  height: 34px;
  transform: translateY(-50%);
}
header .menu-trigger:before {
  display: none;
}
header .menu-trigger span {
  display: block;
  width: 22px;
  height: 2px;
  background: #FFFFFF;
  transition: background 0s .2s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
header .menu-trigger span:before,
header .menu-trigger span:after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #FFFFFF;
  transition-property: transform;
  transition-duration: 0.2s, 0.2s;
  transition-delay: 0.2s, 0s;
}
header .menu-trigger span:before {
  top: -7px;
  transition-property: top, transform;
}
header .menu-trigger span:after {
  bottom: -7px;
  transition-property: bottom, transform;
}
header nav.main-navigation {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background: #001849;
  padding: 25px;
  opacity: 0;
  visibility: hidden;
  z-index: 999;
  transition: 0.2s;
}
header nav.main-navigation ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  text-align: center;
}
header nav.main-navigation ul li {
  display: inline-block;
  width: 100%;
  line-height: 195%;
}
header nav.main-navigation ul li a {
  font-size: 24px;
  display: block;
  width: auto;
  padding: 0 0 8px;
  margin: 0 0 16px;
  text-transform: uppercase;
  color: #FFFFFF;
  font-family: 'Montserrat','Helvetica', Arial,sans-serif;
  font-weight: 700;
  font-style: normal;
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  -moz-transform: perspective(1px) translateZ(0);
  -ms-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  overflow: hidden;
}
header nav.main-navigation ul li a:before {
  content: "";
  position: absolute;
  z-index: -1;
  left: 50%;
  right: 50%;
  bottom: 0;
  background: #FFFFFF;
  height: 4px;
  -webkit-transition-property: "left, right";
  -moz-transition-property: "left, right";
  -ms-transition-property: "left, right";
  transition-property: "left, right";
  -webkit-transition-duration: 0.2s;
  -moz-transition-duration: 0.2s;
  -ms-transition-duration: 0.2s;
  transition-duration: 0.2s;
  -webkit-transition-timing-function: ease-out;
  -moz-transition-timing-function: ease-out;
  -ms-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}
header nav.main-navigation ul li a:hover:before,
header nav.main-navigation ul li a:focus:before,
header nav.main-navigation ul li a:active:before {
  left: 0;
  right: 0;
}
header.menu-open {
  z-index: 99999;
}
header.menu-open .menu-trigger span {
  background: none;
}
header.menu-open .menu-trigger span:before,
header.menu-open .menu-trigger span:after {
  transition-delay: 0s, 0.2s;
}
header.menu-open .menu-trigger span:before {
  top: 0;
  transform: rotate(45deg);
}
header.menu-open .menu-trigger span:after {
  bottom: 0;
  transform: rotate(-45deg);
}
header.menu-open nav.main-navigation {
  opacity: 1;
  visibility: visible;
}
